.cart-alert {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.7;
  position: absolute;
  z-index: 98;
  display: none;
  visibility: hidden;
  padding: 15px;
  transition: all 0.2s ease-in-out;
  transform: translateY(50px);
  opacity: 0;
  color: white;
  border: 1px solid #e4e7ed;
  background: #33b2cf;
}

.cart-alert:before {
  height: 0;
  width: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #e4e7ed;
  position: absolute;
  top: -11px;
  right: 26px;
  content: '';
}

.cart-alert:after {
  height: 0;
  width: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #33b2cf;
  position: absolute;
  top: -10px;
  right: 26px;
  content: '';
}

.cart-alert_hiding {
  visibility: hidden;
  transform: translateY(50px);
  opacity: 0;
}

.cart-alert_visible {
  display: block;
  visibility: visible;
  transform: translateY(0);
  opacity: 1;
}

.cart-alert__link {
  text-decoration: underline;
  color: white;
