  transition: transform 0.2s ease-in-out;
}

@media (max-width: 1024px) {
  .toggle__arrow {
    transform: rotate(180deg);
  }
}

@media (min-width: 1025px) {
  .without-sidebar .toggle__arrow {
    transform: rotate(180deg);
  }
}

@media (max-width: 1024px) {
  .with-sidebar .toggle__arrow {
    transform: rotate(0);
  }
}
